import { createRouter, createWebHistory } from 'vue-router'
import List from '@/views/Board/List.vue'
import Form from '@/views/Board/Form.vue'

import MenuList from '@/views/Menu/List.vue'
import MenuForm from '@/views/Menu/Form.vue'

import PopupList from '@/views/Popup/List.vue'
import PopupForm from '@/views/Popup/Form.vue'

import UserList from '@/views/User/List.vue'
import UserForm from '@/views/User/Form.vue'
import UserManagment from '@/views/User/Managment.vue'

import BusInfo from '@/views/Bus/Bus_info.vue'
import BusInfoEdit from '@/views/Bus/Bus_info_edit.vue'
import BusMain from '@/views/Bus/Bus_main.vue'
import BusMainForm from '@/views/Bus/Bus_main_form.vue'
import BusApplicationList from '@/views/Bus/Bus_application_list.vue'

import AnalysisVisitor from '@/views/Analysis/Visitor.vue'
import AnalysisBus from '@/views/Analysis/Bus.vue'





const routes = [
  {
    path: '/',
    component: List
  },
  {
    path: '/form',
    component: Form
  },
  {
    path:'/menu',
    component: MenuList
  },
  {
    path:'/menu/form',
    component: MenuForm
  },
  {
    path:'/popup',
    component: PopupList
  },
  {
    path:'/popup/form',
    component: PopupForm
  },
  {
    path:'/user',
    component: UserList
  },
  {
    path:'/user/form',
    component: UserForm
  },
  {
    path:'/user/managment',
    component: UserManagment
  },
  {
    path:'/bus/bus_info',
    component: BusInfo
  },
  {
    path:'/bus/bus_info_edit',
    component: BusInfoEdit
  },
  {
    path:'/bus/bus_main_list',
    component: BusMain
  },
  {
    path:'/bus/bus_main_form',
    component: BusMainForm
  },
  {
    path:'/bus/bus_application_list',
    component: BusApplicationList
  },
  {
    path:'/analysis/visotr',
    component: AnalysisVisitor
  },
  {
    path:'/analysis/bus',
    component: AnalysisBus
  },  
  
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  base: 'https://kimjoochan.github.io/testsdf/'
})

export default router
